var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_c('h2',{staticStyle:{"text-align":"center"}},[_c('span',{},[_vm._v("Hai ông Tiến sĩ - Truyện cổ chọn lọc Việt Nam")])]),_c('br'),_c('h3',[_c('em',[_c('span',{},[_vm._v("Chuyện về một cô gái lấy anh học trò nghèo nọ, khi cả hai đang hạnh phúc thì chàng bị bệnh. Vì không muốn ảnh hưởng đến vợ nên dứt áo ra đi. Sau đó, nàng bước đi bước nữa và trở thành một bà quan án. Biết được tung tích nên đã giúp đỡ người chồng cũ của mình. Cuối cùng khi anh khỏi bệnh đỗ làm quan thì bà quan án đã thú thật mọi chuyện và bỏ nhà ra đi.")])])]),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Ngày xưa có chàng học trò nghèo không may mồ côi cha mẹ sớm, gia cảnh khó khăn nhưng chàng chịu khó đèn sách nên sớm nổi tiếng là người đức độ và văn hay chữ tốt. Biết ở làng bên có người con gái xinh xắn nết na, chàng nhờ người mai mối xin cưới nàng làm vợ."),_c('br'),_c('br'),_vm._v(" Cha mẹ cô gái vốn quý trọng người hiền tài, ông bà lại thấy con gái ưng thuận nên vui long nhận lời gả con gái cho chàng học trò."),_c('br'),_c('br'),_vm._v(" Cô gái về nhà chồng được ít lâu thì cha mẹ già nhuốm bệnh theo nhau qua đời. Gia tài các cụ để lại cho con chỉ có nếp nhà tranh và mảnh vườn nhỏ. Nhưng nhờ khéo léo lo toan, vun vén nên người vợ trẻ vẫn giúp chồng yên tâm lo việc học hành."),_c('br'),_c('br'),_vm._v(" Cuộc sống của đôi vợ chồng trẻ vô cùng hạnh phúc. Chẳng mấy chốc đã sắp đến kỳ thi, người vợ lo sắm sửa lều chõng để chồng chuẩn bị đến kinh đô thi tài. Bỗng nhiên chàng học trò lăn ra ốm. Vợ chàng đôn đáo lo thuốc men chữa chạy cho chồng khắp nơi, nhưng căn bệnh hủi của chàng ngày càng nặng hơn."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Nhà cửa, vườn tược đã phải bán hết để lấy tiền chữa trị nhưng bệnh tình của chàng học trò hiểm nghèo khó chữa. Những vết lở loét trên người làm chàng đau đớn ăn không ngon ngủ không yên. Đã thế chàng còn bị mọi người xa lánh, chẳng ai dám đến gần thăm hỏi vì sợ lây bệnh. Thương chồng người vợ trẻ không nề hà sớm tối tận tâm chăm sóc."),_c('br'),_c('br'),_vm._v(" Chàng học trò thương vợ vất cả lại nghĩa chắc mình không qua khỏi nên đã mấy lần lựa lời khuyên vợ để mặc chàng với số phận. Nhưng người vợ nhất định không nghe mà càng thương yêu chàng hơn. Thấy ai mách ở đâu có thuốc hay hay thầy thuốc giỏi, nàng lại lặn lội tìm đến nơi. Khó khăn  mấy nàng cũng không ngại, chỉ một mực mong sao chữa được bệnh cho chồng."),_c('br'),_c('br'),_vm._v(" Một hôm nàng phải đi xa cắt thuốc, khi trở về thì chàng học trò đã bỏ nhà ra đi, chỉ để lại một bức thư vẻn vẹn có mấy chữ: “Nàng hãy quên ta đi và tìm người tử tế để nương tựa”."),_c('br'),_c('br'),_vm._v(" Người vợ khóc lóc vội bỏ đi tìm chồng, ròng rã mấy tháng trời ngược xuôi khắp nơi nhưng nàng vẫn không thấy bóng dáng chàng đâu. Nghe nhiều người nói rằng chàng đã quyên sinh, nàng không tin lời đòn thổi ấy, vẫn mỏi mắt ngóng trông chồng."),_c('br'),_c('br'),_vm._v(" Suốt mấy năm trời đằng đẵng nàng tìm kiếm đợi chờ vẫn không thấy chồng trở về. Cuối cùng nàng đành lập bàn thờ để hương khói cho chồng."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Thấy nàng xinh đẹp và hiền thục, nhiều chàng trai muốn được kết tóc xe tơ nhưng nàng đều từ chối nguyện ở vậy thờ chồng."),_c('br'),_c('br'),_vm._v(" Nhưng có một người con trai quá yêu mến nàng nên kiên trì theo đuổi một hai xin được cưới nàng. Do cảm tấm tình của người con trai, lại thêm mọi người xúm vào khuyên nhủ nàng đành nhận lời đi bước nữa."),_c('br'),_c('br'),_vm._v(" Cuộc sống mới của nàng cũng rất ấm êm, hạnh phúc. Chồng nàng là con nhà gia giáo, chàng chuyên cần sớm khuya đèn sách mong lập được công danh."),_c('br'),_c('br'),_vm._v(" Nhờ có được người vợ hiền, việc học hành của chàng ngày một tấn tới. Ít lâu sau chàng thi đỗ tiến sĩ và được bổ làm quan án sát Sơn Tây. Vợ chàng lại sinh được hai cậu con trai kháu khỉnh, ngoan ngoãn và khỏe mạnh."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_vm._v("Nhà quan án sát tuy không giàu nhưng luôn thuận hòa. Quan là người thanh liêm chính trực nên ông rất được dân chúng mến yêu, tin cậy."),_c('br'),_c('br'),_vm._v(" Còn người chồng cũ của quan bà từ khi trốn vợ bỏ nhà ra đi cứ lang thang nay đây mai đó, sống bằn nghệ bị gậy. Mặc cho bệnh tật hoành hành, chàng chỉ mong đi được càng xa càng tốt. Chàng không muốn trở về, không muốn vợ mình phải khổ vì mình. Chàng lê tấm than lang bạt đến vùng Sơn Tây, đúng lúc có cuộc phát chẩn, chàng lần mò tìm tới."),_c('br'),_c('br'),_vm._v(" Chàng đến gần tốp lính đang luôn tay phát chẩn và nói với họ: Tôi tàn tật lại không nhà không cửa, không nồi niêu nên muốn xin các ngài ít tiền cho tiện."),_c('br'),_c('br'),_vm._v(" Quan án sát nghe mấy người lính bảo rằng có người đến lĩnh chẩn nhưng không nhận gạo mà cứ muốn xin tiền, ông lấy làm lạ nên bèn gọi vào. Sau mấy câu trò chuyện biết người kia vốn là học trò, chữ nghĩa tinh thông, quan thương lắm. Ông lấy tiền ra cho và ngỏ ý muốn giúp đỡ nhưng người kia chỉ xin chút ít tiền rồi từ tạ đi ngay."),_c('br'),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Hôm ấy về dinh, quan án kể cho vợ nghe về người ăn mày, bà ngờ ngợ nghe chồng tả lại hình dáng người kia. Nàng ra chợ dò hỏi tung tích người ăn mày rồi nhờ người đưa đến chỗ anh ta tá túc. Vừa nhìn thấy bóng dáng người ấy nàng giật mình sững sờ nhận ra chồng cũ."),_c('br'),_c('br'),_vm._v(" Trở về nhà, nàng lựa lời xin quan án giúp đỡ chàng ăn mày. Vốn đã có cảm tình với chàng học trò có tài mà bất hạnh, quan án bằng lòng ngay. Ông cho dựng một nếp nhà nhỏ để đón chàng học trò về ở, lại còn sai lo thuốc thang ăn uống cho chàng tử tế. Vợ quan án mừng lắm thỉnh thoảng nàng còn sai người đem thêm quần áo, sách vở cho chàng. Nhưng chàng cũng không lộ cho chồng biết mọi chuyện và cũng không bao giờ cho chàng học trò gặp mặt."),_c('br'),_c('br'),_vm._v(" Một hôm thức dậy lấy nước rửa mặt, chàng học trò giật bắn người khi thấy trong chum có một con rắn trắng bị chết từ bao giờ."),_c('br'),_c('br'),_vm._v(" Thật không ngờ một thời gian sau, những vết lở loét trên người chàng học trò tự nhiên biến mất. Căn bệnh quái ác hành hạ chàng bấy lâu dần dần khỏi hẳn. Chàng trở lại khỏe mạnh và tuấn tú như xưa. Chàng học trò vui mừng khôn xiết vội xin với quan án cho mình được tiếp tục theo việc học hành. Vốn thông minh lại cần cù nên chỉ trong một thời gian ngắn chàng đã quyết tâm lều chõng đi thi."),_c('br'),_c('br'),_vm._v(" Khoa thi năm ấy, chàng học trò đã đỗ tiến sĩ. Nghĩ đến người vợ hiền ở quê nhà đã vì mình mà chịu cực khổ, ông tiến sĩ nóng lòng mong sớm đến ngày được vinh quy bái tổ. Vợ quan án nghe tin chàng học trò đỗ đạt thì vừa vui mừng, vừa đau khổ. Nàng luôn nghĩ mình đã có lỗi với cả hai ông tiến sĩ."),_c('br'),_c('br'),_vm._v(" Một hôm nàng viết một bức thư dài kể hết nỗi lòng của mình cho quan án biết rồi nàng lo cắt đặt việc nhà đâu đó và dặn dò hay con trai cặn kẽ mọi điều. Sáng sớm hôm sau, nàng gạt nước mắt bỏ nhà ra đi. Nàng đi mãi, đi mãi đến khi gặp một ngôi chùa nhỏ mới chịu dừng chân. Và cũng từ đó không ai còn gặp bà quan án nữa."),_c('br'),_c('br'),_vm._v(" Nguồn: Truyện cổ tích Tổng hợp. ")])]),_c('div',{staticClass:"page",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.denketthuc()}}},[_c('KetThuc')],1)]):_vm._e()])])],1),_c('b-col',{attrs:{"cols":"2"}},[_c('RightCom')],1)],1)],1)
